<template>
  <div class="police-message">
    <div class="search">
      <el-form :inline="true" :model="policeMessageForm" ref="policeMessageForm">
        <el-form-item label="小区名称">
          <el-input class="input" size="small" clearable placeholder="请输入"
                    v-model="policeMessageForm.communityName"></el-input>
        </el-form-item>
        <el-form-item label="负责警员">
          <el-input class="input" size="small" clearable placeholder="请输入"
                    v-model="policeMessageForm.officerName"></el-input>
        </el-form-item>
        <el-form-item label="警员编号">
          <el-input class="input" size="small" clearable placeholder="请输入"
                    v-model="policeMessageForm.officerNo"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button size="small" type="primary" @click="handleSearch">查询</el-button>
          <el-button size="small" type="primary" icon="el-icon-edit" @click="newCommunity">新建小区</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="main-table">
      <main-table
        ref="table"
        :border="true"
        v-model="currentPage"
        v-loading="loadingTable"
        :data="tableData"
        :columns="columns"
        :page="page"
        @currentChangePage="currentChange"
      >
      </main-table>
    </div>
    <community-new ref="communityAdd"/>
  </div>
</template>
<script>
  import MainTable from './table/index'
  import Service from './service'
  import CommunityNew from './create/CommunityNew'

  export default {
    components: {
      MainTable,
      CommunityNew
    },
    data() {
      return {
        policeMessageForm: {
          officerName: '',
          officerNo: '',
          communityName: '',
        },
        loadingTable: false,
        page: {
          show: true,
          total: 0,
        },
        currentPage: 1,
        tableData: [],
        columns: [
          {
            label: '序号',
            type: "index"
          },
          {
            label: '行政区划（区、县)',
            prop: 'communityRegion',
          },
          {
            label: '街道',
            prop: 'communityStreet',
          },
          {
            label: '居委会',
            prop: 'communityCommittee',
          },
          {
            label: '小区名称',
            prop: 'communityName',
            // width: 100,
          },
          {
            label: '小区户数',
            prop: 'communityNum',
          },
          {
            label: '负责警员',
            prop: 'officerName',
          },
          {
            label: '操作',
            opration: "edit",
            width: 350,
          }
        ]
      }
    },
    methods: {
      async getOfficerList() {
        this.loadingTable = true
        let params = {
          ...this.policeMessageForm,
          currentPage: this.currentPage
        }
        const data = await Service.getOfficerList(params)
        this.tableData = data.data
        this.page.total = data.totalSize
        this.loadingTable = false
      },
      handleSearch() {
        this.$refs.table.currentChangePage(1)
      },
      policeEdit() {
        this.$refs.dialogAdd.show()
      },
      newCommunity() {
        // 新建小区
        this.$refs.communityAdd.show()
      },
      currentChange(v) {
        this.currentPage = v
        this.getOfficerList()
      },
    },
    mounted() {
      this.getOfficerList()
    },
  }
</script>
<style lang="less">
  .police-message {
    .el-pagination {
      text-align: right;
    }
  }
</style>
