<template>
  <el-dialog title="小区信息修改" :visible="showEdit" @close="close" width="600px">
    <div class="officer-edit">
      <el-form ref="editForm" :inline="true" :model="editForm" :rules="rules" label-position="right"
               label-width="120px">

        <el-form-item label="行政区划" prop="communityRegion">
          <el-input v-model="editForm.communityRegion" placeholder="行政区划"></el-input>
        </el-form-item>

        <el-form-item label="街道" prop="communityStreet">
          <el-input v-model="editForm.communityStreet" placeholder="街道"></el-input>
        </el-form-item>

        <el-form-item label="居委会" prop="communityCommittee">
          <el-input v-model="editForm.communityCommittee" placeholder="居委会"></el-input>
        </el-form-item>
        <el-form-item label="街路巷/小区" prop="communityName">
          <el-input v-model="editForm.communityName" placeholder="街路巷/小区"></el-input>
        </el-form-item>

        <el-form-item style="text-align: right; margin-bottom: 0;padding-left: 100px">
          <el-button size="small" type="primary" @click="submit">确认</el-button>
          <el-button size="small" type="primary" plain @click="close">取消</el-button>
        </el-form-item>
      </el-form>
    </div>
  </el-dialog>

</template>
<script>
  import Service from '../service'

  export default {
    props: {},
    data() {
      return {
        showEdit: false,
        loading: false,
        loadingArea: false,
        editForm: {
          communityId: 0,
          communityRegion: '',
          communityStreet: '',
          communityCommittee: '',
          communityName: '',
        },
        officerLists: [],
        communityList: [],
        rules: {
          communityRegion: [
            {required: true, message: '请输入行政区划', trigger: 'blur'},
          ],
          communityStreet: [
            {required: true, message: '请输入街道', trigger: 'blur'}
          ],
          communityName: [
            {required: true, message: '请输入街路巷/小区', trigger: 'blur'}
          ],
        },
      }
    },
    methods: {
      async saveCommunity() {
        let params = {
          id: this.editForm.communityId,
          communityName: this.editForm.communityName,
          communityRegion: this.editForm.communityRegion,
          communityStreet: this.editForm.communityStreet,
          communityCommittee: this.editForm.communityCommittee,
          communityName: this.editForm.communityName,
        }
        const data = await Service.updateCommunity(params)
        this.$refs.editForm.resetFields()
        this.showEdit = false
        this.$parent.$parent.getOfficerList()
      },
      submit() {
        this.$refs.editForm.validate((valid) => {
          if (valid) {
            this.saveCommunity()
          } else {
            return false;
          }
        })
      },
      close() {
        this.$refs.editForm.resetFields()
        this.showEdit = false
        this.communityList = []
      },
      show(row) {
        this.editForm.communityId = row.communityId
        this.editForm.communityRegion = row.communityRegion
        this.editForm.communityStreet = row.communityStreet
        this.editForm.communityCommittee = row.communityCommittee
        this.editForm.communityName = row.communityName
        this.showEdit = true
      }
    }
  }
</script>
<style lang="less">
  .officer-edit {
    .select, .input {
      width: 300px;
    }

    .add-btn {
      margin-left: 20px;
    }

    .el-tag + .el-tag {
      margin-left: 10px;
    }
  }
</style>
