<template>
  <el-dialog title="新建小区" :visible="showEdit" @close="close" width="800px">
    <div class="officer-edit">
      <el-form ref="editForm" :inline="true" :model="editForm" :rules="rules" label-position="right"
               label-width="150px">

        <el-form-item label="行政区划" prop="communityRegion">
          <el-input v-model="editForm.communityRegion" placeholder="行政区划"></el-input>
        </el-form-item>

        <el-form-item label="街道" prop="communityStreet">
          <el-input v-model="editForm.communityStreet" placeholder="街道"></el-input>
        </el-form-item>

        <el-form-item label="居委会" prop="communityCommittee">
          <el-input v-model="editForm.communityCommittee" placeholder="居委会"></el-input>
        </el-form-item>

        <el-form-item label="街路巷/小区" prop="communityName">
          <el-input v-model="editForm.communityName" placeholder="街路巷/小区"></el-input>
        </el-form-item>

        <el-form-item
          v-for="(officerInfo, index) in editForm.officerInfos"
          :label="'负责民警' + [index + 1] "
          :key="officerInfo.key"
          :prop="'officerInfos.' + index + '.officerName'"
          label-width="140px"
        >

          <el-select
            placeholder="请输入搜索"
            filterable
            clearable
            remote
            @change="choseSelectOfficer(index,officerInfo.officerName)"
            v-model="officerInfo.officerName"
            :loading="loading"
            :remote-method="queryOfficerName"
            style="width: 120px"
          >
            <el-option
              v-for="item in officerLists"
              :key="item.officerName"
              :label="item.officerName"
              :value="item">
              <span style="float: left">{{ item.officerName }}</span>
              <span style="float: right; color: #8492a6; font-size: 13px">{{ item.officerNo }}</span>
            </el-option>
          </el-select>

          <span style="padding-left: 40px;padding-right: 10px">警号</span>
          <el-select
            placeholder="请输入搜索"
            filterable
            clearable
            remote
            @change="choseSelectOfficer(index,officerInfo.officerId)"
            v-model="officerInfo.officerId"
            :loading="loading"
            :remote-method="queryOfficerName"
            style="width: 130px"
          >
            <el-option
              v-for="item in officerLists"
              :key="item.officerNo"
              :label="item.officerNo"
              :value="item">
              <span style="float: left">{{ item.officerName }}</span>
              <span style="float: right; color: #8492a6; font-size: 13px">{{ item.officerNo }}</span>
            </el-option>
          </el-select>

          <span style="padding-left: 40px;padding-right: 10px">电话</span>
          <el-select
            placeholder=""
            filterable
            clearable
            remote
            @change="choseSelectOfficer(index,officerInfo.officerPhone)"
            v-model="officerInfo.officerPhone"
            style="width: 150px"
          >
            <el-option
              v-for="item in officerLists"
              :key="item.officerPhone"
              :label="item.officerPhone"
              :value="item">
              <span style="float: left">{{ item.officerName }}</span>
              <span style="float: right; color: #8492a6; font-size: 13px">{{ item.officerNo }}</span>
            </el-option>
          </el-select>
        </el-form-item>

        <el-form-item style="text-align: right; margin-bottom: 0 ; padding-left: 100px">
          <el-button size="small" type="primary" @click="addOfficer">添加民警</el-button>
        </el-form-item>
        <el-form-item style="text-align: right; margin-bottom: 0 ; padding-left: 151px">
          <el-button size="small" type="primary" @click="submit">确认</el-button>
          <el-button size="small" type="primary" plain @click="close">取消</el-button>
          <el-button size="small" type="primary" plain @click="resetForm">重置</el-button>
        </el-form-item>
      </el-form>
    </div>
  </el-dialog>

</template>
<script>
  import Service from '../service'

  export default {
    props: {},
    data() {
      return {
        showEdit: false,
        loading: false,
        loadingArea: false,
        editForm: {
          communityRegion: '',
          communityStreet: '',
          communityCommittee: '',
          communityName: '',
          officerInfos: [{
            id: 0,
            officerName: '',
            officerId: '',
            officerPhone: '',
          }]
        },
        officerLists: [],
        communityList: [],
        rules: {
          communityRegion:[
            {required: true, message: '行政区划', trigger: 'blur'},
          ],
          communityStreet:[
            {required: true, message: '街道', trigger: 'blur'},
          ],
          communityName: [
            {required: true, message: '街路巷/小区', trigger: 'blur'},
          ],
          officerId: [
            {required: true, message: '请选择警号', trigger: 'blur'}
          ],
          officerName: [
            {required: true, message: '请选择姓名', trigger: 'blur'}
          ],
          officerPhone: [
            {required: true, message: '请选择电话', trigger: 'blur'}
          ],
        },
      }
    },
    methods: {
      addOfficer() {
        this.editForm.officerInfos.push({
          officerName: '',
          officerId: '',
          officerPhone: '',
          key: Date.now()
        });
      },
      async getCommunityList(name) {
        this.loadingArea = true
        const data = await Service.getCommunityList({communityName: name})
        this.communityList = data.data
        this.loadingArea = false
      },
      async queryOfficerName(name) {
        if (name !== '') {
          this.loading = true
          const data = await Service.queryOfficerName({officerName: name})
          this.officerLists = data.data
          this.loading = false
        } else {
          this.officerLists = []
        }
      },
      async saveCommunity() {
        let params = JSON.stringify(this.editForm)
        const data = await Service.saveNewCommunity(this.editForm)
        if (data.code === 200) {
          this.$message({
            type: 'success',
            message: '添加成功!'
          });
        } else {
          this.$message({
            type: 'info',
            message: '添加失败' + data.msg
          });
        }
        this.showEdit = false
        this.$parent.getOfficerList()
      },
      submit() {
        this.$refs.editForm.validate((valid) => {
          if (valid) {
            this.saveCommunity()
          } else {
            return false;
          }
        })
      },
      choseSelectOfficer(index, d) {
        this.editForm.officerInfos[index].officerId = d.officerNo
        this.editForm.officerInfos[index].id = d.id
        this.editForm.officerInfos[index].officerName = d.officerName
        this.editForm.officerInfos[index].officerPhone = d.officerPhone
      },
      close() {
        this.$refs.editForm.resetFields()
        this.showEdit = false
        this.communityList = []
      },
      show() {
        this.showEdit = true
        this.$set(this.editForm, 'officerInfos', [{
          officerName: '',
          officerId: '',
          officerPhone: '',
        }])
      },
      resetForm() {
        this.$refs.editForm.resetFields()
        this.$set(this.editForm, 'officerInfos', [{
          officerName: '',
          officerId: '',
          officerPhone: '',
        }])
      }
    }
  }
</script>
<style lang="less">
  .officer-edit {
    .select, .input {
      width: 300px;
    }

    .add-btn {
      margin-left: 20px;
    }

    .el-tag + .el-tag {
      margin-left: 10px;
    }
  }
</style>
