<template>
  <el-dialog title="添加警员" :visible="showEdit" @close="close" width="800px">
    <div class="officer-edit">
      <el-form ref="editForm" :inline="true" :model="editForm" :rules="rules" label-position="right"
               label-width="150px">

        <el-form-item label="行政区划" prop="communityRegion">
          <el-input v-model="editForm.communityRegion" placeholder="行政区划" :disabled="true"></el-input>
        </el-form-item>

        <el-form-item label="街道" prop="communityStreet">
          <el-input v-model="editForm.communityStreet" placeholder="行政区划" :disabled="true"></el-input>
        </el-form-item>

        <el-form-item label="居委会" prop="communityCommittee">
          <el-input v-model="editForm.communityCommittee" placeholder="行政区划" :disabled="true"></el-input>
        </el-form-item>

        <el-form-item label="街路巷/小区" prop="communityName">
          <el-input v-model="editForm.communityName" placeholder="行政区划" :disabled="true"></el-input>
        </el-form-item>

        <el-form-item
          v-for="(officerInfo, index) in editForm.officerInfos"
          :label="'负责民警' + [index + 1]"
          :key="officerInfo.key"
          :prop="'officerInfos.' + index + '.officerName'"
          label-width="140px"
        >

          <el-select
            placeholder="请输入搜索"
            filterable
            clearable
            remote
            @change="choseSelectOfficer(index,officerInfo.officerName)"
            v-model="officerInfo.officerName"
            :loading="loading"
            :remote-method="queryOfficerName"
            style="width: 120px"
          >
            <el-option
              v-for="item in officerLists"
              :key="item.officerName"
              :label="item.officerName"
              :value="item">
              <span style="float: left">{{ item.officerName }}</span>
              <span style="float: right; color: #8492a6; font-size: 13px">{{ item.officerNo }}</span>
            </el-option>
          </el-select>

          <span style="padding-left: 40px;padding-right: 10px">警号</span>
          <el-select
            placeholder="请输入搜索"
            filterable
            clearable
            remote
            @change="choseSelectOfficer(index,officerInfo.officerNo)"
            v-model="officerInfo.officerNo"
            :loading="loading"
            :remote-method="queryOfficerName"
            style="width: 120px"
          >
            <el-option
              v-for="item in officerLists"
              :key="item.officerNo"
              :label="item.officerNo"
              :value="item">
              <span style="float: left">{{ item.officerName }}</span>
              <span style="float: right; color: #8492a6; font-size: 13px">{{ item.officerNo }}</span>
            </el-option>
          </el-select>

          <span style="padding-left: 40px;padding-right: 10px">电话</span>
          <el-select
            placeholder="请输入搜索"
            filterable
            clearable
            remote
            @change="choseSelectOfficer(index,officerInfo.officerPhone)"
            v-model="officerInfo.officerPhone"
            :loading="loading"
            :remote-method="queryOfficerPhone"
            style="width: 140px"
          >
            <el-option
              v-for="item in officerLists"
              :key="item.officerPhone"
              :label="item.officerPhone"
              :value="item">
              <span style="float: left">{{ item.officerName }}</span>
              <span style="float: right; color: #8492a6; font-size: 13px">{{ item.officerNo }}</span>
            </el-option>
          </el-select>

          <span style="padding-left: 20px;padding-right: 5px">
            <el-button type="primary" icon="el-icon-delete" size="mini" @click="deleteOne(index)"></el-button>
          </span>

        </el-form-item>

        <el-form-item style="text-align: right; margin-bottom: 0 ; padding-left: 100px">
          <el-button size="small" type="primary" @click="addOfficer">添加民警</el-button>
        </el-form-item>
        <el-form-item style="text-align: right; margin-bottom: 0 ; padding-left: 151px">
          <el-button size="small" type="primary" @click="addCommunityOfficers">确认</el-button>
          <el-button size="small" type="primary" plain @click="close">取消</el-button>
          <el-button size="small" type="primary" plain @click="resetForm">重置</el-button>
        </el-form-item>
      </el-form>
    </div>
  </el-dialog>

</template>
<script>
  import Service from '../service'

  export default {
    props: {},
    data() {
      return {
        showEdit: false,
        loading: false,
        loadingArea: false,
        editForm: {
          communityId: '',
          communityRegion: '',
          communityStreet: '',
          communityCommittee: '',
          communityName: '',
          officerInfos: [{
            officerId: 0,
            officerName: '',
            officerNo: '',
            officerPhone: '',
          }]
        },
        officerLists: [],
        communityList: [],
        rules: {
          communityName: [
            {required: true, message: '请选择小区名称', trigger: 'blur'},
          ],
          officerId: [
            {required: true, message: '请选择警号', trigger: 'blur'}
          ],
          officerName: [
            {required: true, message: '请选择姓名', trigger: 'blur'}
          ],
          officerPhone: [
            {required: true, message: '请选择电话', trigger: 'blur'}
          ],
        },
      }
    },
    methods: {
      deleteOne(indexY) {
       //this.$set(this.editForm, 'officerInfos',  this.editForm.officerInfos.splice(indexY-1,1))
        this.editForm.officerInfos.splice(indexY,1)
      },
      addOfficer() {
        this.editForm.officerInfos.push({
          officerName: '',
          officerId: 0,
          officerNo: '',
          officerPhone: '',
          key: Date.now()
        });
      },
      async getCommunityList(name) {
        this.loadingArea = true
        const data = await Service.getCommunityList({communityName: name})
        this.communityList = data.data
        this.loadingArea = false
      },
      async queryOfficerName(name) {
        if (name !== '') {
          this.loading = true
          const data = await Service.queryOfficerName({officerName: name})
          this.officerLists = data.data
          this.loading = false
        } else {
          this.officerLists = []
        }
      },
      async queryOfficerPhone(name) {
        if (name !== '') {
          this.loading = true
          const data = await Service.queryOfficerPhone({officerPhone: name})
          this.officerList = data.data
          this.loading = false
        } else {
          this.officerList = []
        }
      },
      async addCommunityOfficers() {
        let officerIds = []

        for (let okey in this.editForm.officerInfos) {
          officerIds.push(this.editForm.officerInfos[okey].officerId)
        }

        let newData = {
          communityId: this.editForm.communityId,
          officerIds: officerIds.join(',')
        }
        const data = await Service.saveCommunity(newData)
        this.showEdit = false
        this.$parent.$parent.getOfficerList()
      },
      submit() {
        this.$refs.editForm.validate((valid) => {
          if (valid) {
            this.addCommunityOfficers()
          } else {
            return false;
          }
        })
      },
      choseSelectOfficer(index, d) {
        this.editForm.officerInfos[index].officerNo = d.officerNo
        this.editForm.officerInfos[index].officerId = d.id
        this.editForm.officerInfos[index].officerName = d.officerName
        this.editForm.officerInfos[index].officerPhone = d.officerPhone
      },
      close() {
        this.$refs.editForm.resetFields()
        this.showEdit = false
        this.communityList = []
      },
      async show(index, row) {
        this.showEdit = true
        const data = await Service.getOfficersInfoById({id: row.communityId})
        if (data.code != 200) {
          this.$message({
            type: 'info',
            message: '接口超时!'
          });
          return
        }
        let handleData = data.data.map(item => {
          return {
            officerNo: item.officerNo,
            officerPhone: item.officerPhone,
            officerName: item.officerName,
            officerId: item.id
          };
        });
        this.$set(this.editForm, 'officerInfos', handleData)
        this.editForm.communityRegion = row.communityRegion
        this.editForm.communityStreet = row.communityStreet
        this.editForm.communityCommittee = row.communityCommittee
        this.editForm.communityName = row.communityName
        this.editForm.communityId = row.communityId
      },
      resetForm() {
        this.$refs.editForm.resetFields()
        this.$set(this.editForm, 'officerInfos', [{
          officerName: '',
          officerId: '',
          officerPhone: '',
        }])
      }
    }
  }
</script>
<style lang="less">
  .officer-edit {
    .select, .input {
      width: 300px;
    }

    .add-btn {
      margin-left: 20px;
    }

    .el-tag + .el-tag {
      margin-left: 10px;
    }
  }
</style>
