<template>
  <div>
    <el-table
      style="width: 100%"
      :data="data"
      :border="border"
      :stripe="stripe"
      :span-method="spanMethod"
      :highlight-current-row="highlightCurrentRow"
      :default-sort="defaultSort"
      @sort-change="sortChange"
      @current-change="currentChangeRow"
      ref="table"

      row-key="treeId"
      :tree-props="{children: 'children'  }"
    >
      <template v-for="column in columns">
        <slot v-if="column.slot" :name="column.slot"></slot>
        <el-table-column
          v-else-if="column.opration"
          :prop="column.prop"
          :label="column.label"
          :width="column.width"
          :sortable="column.sortable"
          :show-overflow-tooltip="column.tooltip"

        >
          <template slot-scope="scope">
              <el-button
                size="mini"
                @click="handleEdit(scope.$index, scope.row)">编辑
              </el-button>
              <el-button
                size="mini"
                @click="newOfficer(scope.$index, scope.row)">警员管理
              </el-button>
              <el-button
                size="mini"
                @click="importData(scope.$index, scope.row)">导入数据
              </el-button>
              <el-button
                size="mini"
                type="danger"
                @click="handleDeleteCommunity(scope.$index, scope.row)">删除小区
              </el-button>
          </template>
        </el-table-column>

        <el-table-column
          v-else
          :type="column.type"
          :prop="column.prop"
          :label="column.label"
          :width="column.width"
          :sortable="column.sortable"
          :show-overflow-tooltip="column.tooltip"
        />
      </template>
    </el-table>
    <div class="page" :class="page.align">
      <el-pagination
        background
        layout="total, prev, pager, next"
        v-if="page.show"
        :total="page.total"
        :page-size="10"
        :page-sizes="page.pageSizes"
        :pager-count="page.pageCount"
        :current-page.sync="currentPage"
        @size-change="sizeChange"
        @current-change="currentChangePage"
      >
      </el-pagination>
    </div>
    <EditCommunity ref="EditCommunity"/>
    <EditCommunityOfficers ref="EditCommunityOfficers"/>

    <el-dialog
      :title="title"
      :visible.sync="dialogVisible"
      width="600px"
      @close="handleClose">
      <el-form label-position="right" label-width="100px">
        <el-form-item label="小区名称: ">
          <el-input v-model="commiuntyName" disabled width="50px"></el-input>
        </el-form-item>
        <el-upload
          :action="uploadUrl"
          :limit="1"
          :on-success="onSuccess"
          accept=".xls,.xlsx"
          :file-list="fileList">
          <el-button size="small" type="primary">点击上传</el-button>
          <div slot="tip" class="el-upload__tip">只可上传.xlsx 、.xls文件</div>
        </el-upload>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
  import EditCommunity from '../edit/EditCommunity'
  import EditCommunityOfficers from '../edit/EditCommunityOfficers'
  import Service from '../service'

  export default {
    components: {
      EditCommunity,
      EditCommunityOfficers
    },
    props: {
      border: {
        type: Boolean,
        default: false,
      },
      data: {
        type: Array,
        default() {
          return []
        },
      },
      columns: {
        type: Array,
        default() {
          return []
        },
      },
      page: {
        type: Object,
        default() {
          return {}
        },
      },
      stripe: {
        type: Boolean,
        default: false,
      },
      highlightCurrentRow: {
        type: Boolean,
        default: false,
      },
      spanMethod: Function,
      defaultSort: {
        type: Object,
        default() {
          return {}
        },
      },
      sortChange: {
        type: Function,
        default() {
          return null
        },
      },
    },
    data() {
      return {
        commiuntyName: "",
        fileList: [],
        currentPage: 0,
        dialogVisible: false,
        title: '上传房屋交易信息',
        uploadUrl:
          '/community/officer/uploadCommunityInfo',
      }
    },
    methods: {
      sizeChange(val) {
        this.$emit('sizeChange', val)
      },
      currentChangePage(val) {
        this.currentPage = val
        this.$emit('currentChangePage', val)
      },
      currentChangeRow(val) {
        this.$emit('currentChangeRow', val)
      },
      resetPage() {
        this.currentPage = 1
      },
      newOfficer(index, row) {
        this.$refs.EditCommunityOfficers.show(index, row)
      },
      handleEdit(index, row) {
        this.$refs.EditCommunity.show(row)
      },
      async handleDeleteCommunity(index, row) {
        this.$confirm('此操作将删除小区信息及所有相关警员信息, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(async () => {
          const data = await Service.deleteCommunity({communityId: row.communityId})
          if (data.code === 200) {
            this.$message({
              type: 'success',
              message: '删除成功!'
            });
          } else {
            this.$message({
              type: 'info',
              message: '删除失败' + data.msg
            });
          }
          this.$parent.getOfficerList()
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
      },
      importData(index, row) {
        console.log(index, row)
        this.commiuntyName = row.communityName
        this.dialogVisible = true

      },
      onSuccess() {
        this.$message.success('文件上传成功！')
        this.handleClose()
      },
      handleClose() {
        this.dialogVisible = false
        this.fileList = []
      },
      async handleDeleteOfficer(index, row) {
        this.$confirm('此操作将删除该小区和警员关系, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(async () => {
          const data = await Service.detachRelation({
            communityId: row.communityId,
            officerIds: [row.id]
          })
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
          this.$parent.getOfficerList()
        }).catch((e) => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
      },
    },
  }
</script>

<style lang="less" scoped>
  .page {
    margin-top: 15px;

    &.center {
      text-align: center;
    }

    &.left {
      text-align: center;
    }

    &.right {
      text-align: right;
    }
  }
</style>
